<template>
  <div class="classifyList">
    <van-nav-bar
      ref="toolbar"
      style="background:#f6f6f6"
      left-arrow
      :title="major"
      @click-left="back"
    />
    <div class="classifyList-box">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <div
          class="list-box"
          v-for="(item,index) in ClassifyListData"
          :key="index"
          @click="goDetail(item._id)"
        >
          <div class="img-box">
            <img class="auto-img" :src="item.cover" alt />
          </div>
          <div class="text-box">
            <div class="title-box">
              <div class="title">{{item.title}}</div>
              <div class="author">{{item.author}}</div>
            </div>
            <div class="shorIntro">{{item.shortIntro}}</div>
            <div class="tags">
              <span>{{item.retentionRatio}}%留存</span>
              {{item.tags}}
            </div>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import "../assets/less/classifyList.less";
export default {
  data() {
    return {
      start: 0,
      limit: 5,
      step: 5,
      major: "",
      loading: false,
      finished: false,
      ClassifyListData: [],
      list: [],
    };
  },
  created() {
    // this.getClassifyListData();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },

    // 获取书籍数据
    getClassifyListData() {
      // console.log(this.$route.query);
      let gender = this.$route.query.gender;
      this.major = this.$route.query.major;
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: `/category-info?gender=${gender}&type=hot&major=${this.major}&start=${this.start}&limit=${this.limit}`,
      })
        .then((result) => {
          this.$toast.clear();
          this.loading = false;
          this.start += this.step;
          this.limit += this.step;
          // console.log(result);
          // console.log(this.start, this.limit);
          if (result.data.books.length >= 0) {
            let data = result.data.books;
            data.map((v) => {
              v.cover = unescape(v.cover.slice(7));
              if (v.tags.length > 3) {
                v.tags = v.tags.splice(0, 4).join("·");
              } else {
                v.tags = v.tags.join("·");
              }
              this.ClassifyListData.push(v);
            });
          } else {
            this.finished = true;
          }
          // console.log(this.ClassifyListData);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 懒加载
    onLoad() {
      // 异步更新数据
      this.getClassifyListData();
    },

    // 点击书籍，根据书籍id获取书籍详情，前往书籍详情页面
    goDetail(id) {
      this.$store.dispatch("detailStore/setBookIdListById", id);
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios
        .all([
          this.axios({
            method: "GET",
            url: "/book-info/" + id,
          }),
          this.axios({
            method: "GET",
            url: "/book-sources?view=summary&book=" + id,
          }),
        ])
        .then((result) => {
          this.$toast.clear();
          this.$store.dispatch("detailStore/setBookDetail", result[0].data);
          this.$store.dispatch(
            "detailStore/setBookOriginIdListById",
            result[1].data[0]._id
          );
          this.$router.push({ name: "Detail" });
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>
